@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  --maxWidth: 1200px;
  --gap: 2rem;
}

html, body {
  height: 100%;
  width: 100%;
  font-family: 'Playfair', sans-serif;
  font-family: 'Raleway', sans-serif;
  color: #bdc3c7;
  background-color: #323133;
  }

.headings {
  padding-top: 70px;
}

.App {
  background-image: linear-gradient(to bottom right, 
    #434343,
    #3B3B3B,
    #323232,
    #2A2A2A,
    #222222,
    #191919,
    #111111,
    #080808,
    #000000
    )
}


/*  ---------------- NAVBAR FORMATTING -------------------- */
.navbar {
  padding:.8rem;
  background-color: rgba(32,33,36,0.96);
  opacity: 0.5;
  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(10000px) saturate(225%);
  z-index: 2;
}

.navbar-nav li {
  position: relative;
  text-align: right;
  padding-right: 5px;
  background-color: transparent;
}
.nav-link {
  font-size: 1em !important;
  background-color: transparent;
}


.btn-primary {
  background-color: transparent;
  border: 1px solid #95a5a6;
}
.btn-primary:hover {
  background-color: #4d4d4d;
  border: 1px solid #7f8c8d;
}

/*  ---------------- Card FORMATTING -------------------- */

/* flexbox */
.card-layout{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;
}

/* spacing between card on wrap */
.card-layout > * {
  margin: 10px;
}

.card {
  border-radius: 10pt;
  text-align: center;
  background-color: #434A53;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.288);
  width: 250px;
  height: 400px;
}

.card img {
  border-top-left-radius: 10pt;
  border-top-right-radius: 10pt;
}



/*  ---------------- Footer FORMATTING -------------------- */

footer {
  /* background-color: #323133; */
  color: #bdc3c7;
}

footer a {
  color: #bdc3c7;
}

/*---Firefox Bug Fix --*/
.carousel-item {
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-backface-visibility: visible;
  backface-visibility: visible;
  height: 79vh;
}


/*--- Bootstrap Padding fix--*/
[class*="col-"] {
    padding: 1rem;
}




/* Make things pop up when you hover over them */
.hvr-grow {
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0,0,0,0);
        transition-duration: 0,05s;
        transition-property: -webkit-transform;
        transition-property: transform;
        transition-property: transform, -webkit-transform;
      }
.hvr-grow:hover, .hvr-grow:focus, .hvr-grow:active {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.home-slide {
    height: 500px !important;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .slide-image {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .home-slider .content {
    position: relative;
    z-index: 2;
  }

/*  ---------------- About Me FORMATTING -------------------- */

.about {
    text-align: center;
    vertical-align: center;
  }

.profiles {
    text-align: center;
    padding-top: 1em;
}

.profiles img {
    width: 50px;
    margin: 1em;
    height: auto;
    border-radius: 50%;
    box-shadow: 0px 0px 20px 5px rgba(0,0,0,0.15);
}
  
.personal-photo {
    border-radius: 50%;
    width: 200px;
    height: auto;
    box-shadow: 0px 10px 20px 10px rgba(0,0,0,0.30);
}

.swiper-slide {
    width: auto;
}

.logo-slide {
    width: 70%;
    max-width: 150px;
    max-height: 150px;
    height: auto;
}

.image-slide {
    border-radius: .5em;
    margin-bottom: 50px;
    width: 80%;
    max-width: 800px;
    box-shadow: 0px 10px 20px 10px rgba(0,0,0,0.30);
    -webkit-box-reflect: below 2px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(70%, transparent), to(rgba(255,255,255,0.5)));
}

.slide-container {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;
}

.image-slider {
    /* padding-bottom: 200px; */
}


/*  ---------------- Connect FORMATTING -------------------- */

.connect {
    padding: 1em;
    /* background-color: #3C3B3D; */
}

.connect-body {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    vertical-align: center;
    text-align: center;
    justify-content: center;
    margin: 0 auto;
    padding: 1em;
}

.connect-title {
    color:#bdc3c7;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
}

.profiles {
    text-align: center;
    padding-top: 1em;
}

.profiles img {
    width: 50px;
    height: auto;
    border-radius: 50%;
    box-shadow: 0px 0px 20px 5px rgba(0,0,0,0.15);
}

.connect-text {
    padding-bottom: 3em;
}


.connect-form {
    width: 500px;
    text-align: left;
    color: #7eb7cc;
    padding-top: 5px;
    padding-bottom: 50px;
    padding-left: 50px;
    padding-right: 50px;
}

.connect-form button {
    color: #7eb7cc;
    border-color: #7eb7cc;
}

.connect-form-button {
    text-align: center;
}

.submit-message {
    color: #3C3B3D;
    box-shadow: 0px 0px 20px 5px rgba(0,0,0,0.15);
}
.upper{
    width: 100%;
    text-align: center;
}

/* the div that the game is in */
.App {
    position: relative;
    display: block;
}
.gameContainer{
    margin: 20px;
    position: relative;
}

.menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    width: 110px;
    height: 250px;            
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -200px;
    margin-left: -55px;

}

.menu button {
    margin: 5px;
}

.reset {
    width: 100px;
    height: 30px;            
    position: absolute;
    top: 25%;
    left: 60%;
    margin-top: -15px;
    margin-left: -50px;

}

.hidden {
    display: none;
}


.rules {
    color: white;
    vertical-align: middle;
    width: 200px;
    height: 250px;            
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -200px;
    margin-left: -100px;
    background-color: rgba(0, 0, 0, 0.5);
}
.game-header {
    text-align: center;
}

.game-header h1 {
    margin-top: 65px;
}

.game-footer {
    text-align: center;
}
.game-footer button {
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 20px 5px rgba(0,0,0,0.50);
}

.runner {
    margin: auto;
}

/* PONG CSS */

.pong {
    text-align: center;
}

.pong button {
    color: #7eb7cc;
    border-color: #7eb7cc;
    background-color: rgba(0, 0, 0, 0.5);
}
/* ------------------ Blog Post Layout -------------------------- */

.page-wrap {
    display: block;
    position: -webkit-sticky;
    position: sticky;
    min-height: 1000px;
    max-width: var(--maxWidth);
    margin: 0 auto;
    margin-top: 10%;
    padding: 0 var(--gap);
  }
  
  .post-scroll {
    position: static;
    overflow-y: scroll;
    overflow: hidden;
  }
  
  /* set body scroll */
  /* body.noscroll{
    position: fixed;
    overflow: hidden;
  } */
  
  .page-wrap > * {
    box-sizing: border-box;
  }
  
  .post {
    border-radius: inherit;
    object-fit: contain;
    margin: 0 auto;
    border-radius: 1em;
    min-height: 90%;
    /* background-color:rgba(32,33,36,0.96); */
  }
  
  #post-header{
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    object-fit: cover;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 1em;
  }
  
  #post-author-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: rgba(32,33,36,0.5);
  }
  #post-author-info > * {
    margin-left: 10px;
  }
  
  #post-author-info p {
    margin-top: 1em;
    color: #FFF;
    align-self: center;
  }
  
  /* Header */
  .post h2 {
    align-self: center;
    color: #fff;
    background-color: rgba(32,33,36,0.5);
  }
  
  #content {
    min-height: 2000px;
    border-radius: 1em;
    color: #3C3B3D;
    text-align: center;
    background-color: #fff;
  }
